import { useState, useRef, forwardRef } from "react";
import { ModalCarpet } from "../../styledCpns/Layout";
import { Button } from "../../styledCpns/Inputs";
import { Text } from "../../styledCpns/DataDisplay";
import { LoadBtn, StyledInner, CloseBtn, Divider } from "./styles";
import { useCloseModal } from "../../helpers/hooks";

export default function Upload({ loadData }) {
  const [optionsOn, setOptionsOn] = useState(false);
  const [err, setErr] = useState(false);

  const tryToLoadFromLocalStorage = () => {
    let data = localStorage.getItem("GDC_Data");
    if (data) {
      data = JSON.parse(data);
      loadData(data);
      setOptionsOn(false);
    } else {
      setErr(true);
    }
  };

  const chooseRef = useRef();
  const upload = () => {
    const file = chooseRef.current.files[0];
    const reader = new FileReader();
    if (file?.type.match(/text.*/)) {
      reader.onload = function (event) {
        const data = JSON.parse(event.target.result);
        loadData(data);
        setOptionsOn(false);
      };
      reader.readAsText(file);
    }
  };

  return (
    <div>
      <LoadBtn className="pointer" onClick={() => setOptionsOn(true)}>
        <i className="fas fa-upload mr-2" />
        <Text fontWeight={700}>Upload</Text>
      </LoadBtn>
      {optionsOn && (
        <ForwardedOptions
          ref={chooseRef}
          err={err}
          tryToLoadFromLocalStorage={tryToLoadFromLocalStorage}
          upload={upload}
          close={() => {
            setOptionsOn(false);
            setErr(false);
          }}
        />
      )}
    </div>
  );
}

const ForwardedOptions = forwardRef(Options);

function Options(props, ref) {
  const closeRef = useCloseModal(props.close);
  return (
    <ModalCarpet>
      <StyledInner ref={closeRef} className="pt-2 pb-3">
        <CloseBtn className="ml-auto mr-2 mb-2" onClick={props.close} />
        <div className="option flex-col align-center">
          <Text variant="h6" fontWeight={700}>
            Load from Local Storage
          </Text>
          {props.err && (
            <Text color="red">! There's no data in your Local Storage !</Text>
          )}
          <Button
            className="my-1"
            variant="positive"
            onClick={props.tryToLoadFromLocalStorage}
          >
            Load
          </Button>
        </div>
        <Divider />
        <div className="option flex-col align-center">
          <Text variant="h6" fontWeight={700}>
            Upload a .TXT file
          </Text>
          <input
            ref={ref}
            style={{ display: "none" }}
            type="file"
            accept="text/*"
            onChange={props.upload}
          />
          <Button
            className="my-1"
            variant="positive"
            onClick={() => {
              ref.current.click();
            }}
          >
            Choose File
          </Button>
        </div>
      </StyledInner>
    </ModalCarpet>
  );
}
