import { StyledTable } from "../../CommonCpns";

export default function CompareTable({ obj1, obj2 }) {
  return (
    <StyledTable>
      <tbody>
        <tr>
          <th></th>
          <th className="lightGold-text">Old</th>
          <th className="lightGold-text">New</th>
        </tr>
        {Object.keys(obj1).map((type, i) => (
          <tr key={i}>
            <td style={{ textTransform: "capitalize" }}>{type}</td>
            <td>{obj1[type]}</td>
            <td>{obj2[type]}</td>
          </tr>
        ))}
      </tbody>
    </StyledTable>
  );
}
