import styled from "styled-components";
import { Collapse } from "@material-ui/core";
import { useMemo, useState } from "react";
import { Box, ModalCarpet } from "../styledCpns/Layout";
import {
  Text,
  ColoredSpan,
  List,
  CollapseHeading
} from "../styledCpns/DataDisplay";
import { Button, IconBtn, TextArea } from "../styledCpns/Inputs";
import artifacts from "../data/artifacts";
import { colorCodes } from "../data/misc";
import { useCloseModal } from "../helpers/hooks";
import encodeExportedData from "../helpers/encode";

export function EquippedChar({ user }) {
  return (
    <Box className="px-4 py-1" bgColor="#ffe7bb">
      <Text fontWeight={700} color="black">
        Equipped: {user}
      </Text>
    </Box>
  );
}

export const ActionBtn = ({ icon, ...others }) => (
  <IconBtn icon={icon} size="2rem" fontSize="1rem" {...others} />
);

export function OneBtnBar(props) {
  const { className } = props;
  return (
    <div className={"flex justify-center" + (className ? " " + className : "")}>
      <Button
        className="glow-on-hover"
        variant={props.variant}
        onClick={props.handleClick}
      >
        {props.text}
      </Button>
    </div>
  );
}

export function YinYangBtnBar(props) {
  const { className } = props;
  return (
    <div className={"flex justify-center" + (className ? " " + className : "")}>
      <Button
        className="mr-5 glow-on-hover"
        variant="negative"
        onClick={props.leftClicked}
      >
        {props.leftText}
      </Button>
      <Button
        className="glow-on-hover"
        variant="positive"
        onClick={props.rightClicked}
      >
        {props.rightText}
      </Button>
    </div>
  );
}

export function SetBonus({ sets }) {
  return (
    <div>
      <Text variant="h6" color="orange" fontWeight={700}>
        Set Bonus
      </Text>
      {sets.length > 0 ? (
        sets.map((set, i) => {
          const content = [];
          for (let i = 0; i <= set.bonusLv; i++) {
            content.push(
              <li key={i} className="mt-1">
                <ColoredSpan color="orange">
                  {artifacts[set.name].setBnes[i].type}:
                </ColoredSpan>{" "}
                <ColoredSpan color="green">
                  {artifacts[set.name].setBnes[i].desc}
                </ColoredSpan>
              </li>
            );
          }
          return (
            <div key={i} className="mt-1">
              <Text variant="h6" fontWeight={700} color="green">
                {set.name}
              </Text>
              <List className="pl-4">{content}</List>
            </div>
          );
        })
      ) : (
        <Text color="rgb(185, 185, 185)" fontWeight={700}>
          No Set Bonus
        </Text>
      )}
    </div>
  );
}

const StyledSvg = styled.svg`
  width: 1.25rem;
  height: 1.25rem;
  fill: ${(props) => colorCodes[props.rarity]};
`;

export const Star = (props) => (
  <StyledSvg viewBox="0 0 24 24" rarity={props.rarity}>
    <path d="M12,17.27L18.18,21L16.54,13.97L22,9.24L14.81,8.62L12,2L9.19,8.62L2,9.24L7.45,13.97L5.82,21L12,17.27Z" />
  </StyledSvg>
);

export const StarLine = ({ rarity, className = "" }) => {
  const result = [];
  for (let i = 0; i < (rarity === "Gold" ? 5 : 4); i++) {
    result.push(<Star key={i} rarity={rarity} />);
  }
  return <div className={"flex align-center " + className}>{result}</div>;
};

export const TalentXtraLv = ({ xtraLv }) =>
  xtraLv ? (
    <Text className="ml-2" fontWeight={700} color="green">
      +{xtraLv}
    </Text>
  ) : null;

export function Confirmation(props) {
  const { close, otherBtn } = props;
  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="p-3 relative"
        width="22.5rem"
        borderRadius="1rem"
        bgColor="cardBg3"
      >
        <div className="py-3">
          <Text style={{ fontSize: "1.375rem" }} align="center">
            {props.message}
          </Text>
        </div>
        <YinYangBtnBar
          className="mt-3"
          leftText={otherBtn?.text || "Cancel"}
          leftClicked={() => {
            if (otherBtn?.handleClick) otherBtn.handleClick();
            close();
          }}
          rightText="Confirm"
          rightClicked={() => {
            props.confirm();
            close();
          }}
        />
      </Box>
    </ModalCarpet>
  );
}

const StyledCollapse = styled(Collapse)`
  padding: 0 1rem 0 0.5rem;
  & .mb-m1:first-child {
    margin-top: -0.5rem;
  }
`;

export function ExpandableList(props) {
  const [expanded, setExpanded] = useState([]);
  return (
    <div>
      {props.headingList.map((heading, i) => (
        <div key={i} className={expanded[i] ? "mb-4" : ""}>
          <CollapseHeading
            className={"mb-2p5" + (expanded[i] ? " active" : "")}
            onClick={() =>
              setExpanded((prev) => {
                const newEpd = [...prev];
                newEpd[i] = !newEpd[i];
                return newEpd;
              })
            }
          >
            {heading}
          </CollapseHeading>
          <StyledCollapse in={Boolean(expanded[i])}>
            {props.contentList[i]}
          </StyledCollapse>
        </div>
      ))}
    </div>
  );
}

export const StyledTable = styled.table`
  margin-bottom: 0.5rem;
  min-width: 100%;
  border-collapse: collapse;
  font-size: 0.875rem;
  line-height: 1.43;
  th,
  td {
    padding: 0.25rem 0.5rem;
    border-left: 1px solid ${colorCodes.cardBg1};
    border-right: 1px solid ${colorCodes.cardBg1};
    cursor: default;
  }
  td:first-child {
    font-weight: 700;
  }
  td:not(:first-child) {
    text-align: right;
  }
  tr:nth-child(2n) {
    background-color: ${colorCodes.cardBg2};
  }
  tr:nth-child(2n + 1) {
    background-color: ${colorCodes.cardBg1};
  }
  tr:first-child {
    background-color: #01121b;
  }
  tr:not(:first-child):hover {
    background-color: #8a1200;
  }
  .lightGold-text {
    color: ${colorCodes.lightGold};
  }
  .sticky-col {
    position: sticky;
    z-index: 2;
    left: 0;
    min-width: 8.5rem;
    background-color: inherit;
  }
  .limited-width {
    width: 8.5rem;
  }
  .fas {
    position: absolute;
    top: 50%;
    right: 0.375rem;
    margin-top: -0.425rem;
    font-size: inherit;
  }
  .fa-long-arrow-alt-up,
  .higher {
    color: ${colorCodes.green};
  }
  .fa-long-arrow-alt-down {
    color: ${colorCodes.lightRed};
  }
  .tooltip_wrapper {
    padding-right: 1.125rem !important;
    min-width: 5rem;
  }
  .tooltip_wrapper:hover .tooltip {
    display: block;
  }
  .tooltip {
    position: absolute;
    bottom: calc(50% + 0.625rem);
    right: 1rem;
    z-index: 1;
    display: none;
    padding: 0.25rem 0.5rem 0.125rem;
    border-radius: 0.25rem;
    background: black;
  }
  .lower {
    color: #ff5d61;
  }
`;

export function DmgTable(props) {
  const { index, closed } = props;
  return (
    <div className={"flex-col" + props.mb}>
      <Box
        className="mx-auto px-3 mb-2 flex align-center cursor-default"
        borderRadius="1rem"
        bgColor="orange"
        onClick={() =>
          props.setClosed((prev) => {
            const newC = [...prev];
            newC[index] = !newC[index];
            return newC;
          })
        }
      >
        <Text variant="h5" fontWeight={700} color="black">
          {props.heading}
        </Text>
        <Text className="ml-2" variant="subtitle1" color="black">
          <i className={"fas fa-chevron-" + (closed[index] ? "down" : "up")} />
        </Text>
      </Box>
      <Collapse in={!closed[index]}>
        <div className="custom-sb">
          <StyledTable>{props.children}</StyledTable>
        </div>
      </Collapse>
    </div>
  );
}

export function ShareLayout(props) {
  const { close } = props;
  const [status, setStatus] = useState(0);
  const ref = useCloseModal(close);
  const encoded = useMemo(() => encodeExportedData(props.data), []);

  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="px-4 pt-3 pb-4 flex-col white-glow-shadow"
        width="21.75rem"
        borderRadius="0.5rem"
        bgColor="cardBg1"
      >
        {/* <Text variant="h3" align="center">
          COMING SOON
        </Text> */}
        <Text
          className="mb-2 px-2"
          variant="h5"
          color="orange"
          fontWeight={700}
          align="center"
        >
          Share "{props.setupName}"
        </Text>
        <TextArea
          id="shared_data"
          className="p-2"
          rows="15"
          value={encoded}
          readOnly
        />
        {Boolean(status) && (
          <Text
            className="mt-2"
            align="center"
            color={status === 1 ? "Green" : "lightRed"}
          >
            {status === 1
              ? "Successfully copied to Clipboard."
              : "We're sorry. Your browser does not allow/support this function."}
          </Text>
        )}
        <YinYangBtnBar
          className="mt-3"
          leftText="Cancel"
          leftClicked={close}
          rightText="Copy"
          rightClicked={() => {
            navigator.clipboard
              .writeText(document.getElementById("shared_data").value)
              .then(
                () => setStatus(1),
                () => setStatus(2)
              );
          }}
        />
      </Box>
    </ModalCarpet>
  );
}
