import { useContext } from "react";
import { SetDBCt } from "../../../App";
import { Box, CardCarpet } from "../../../styledCpns/Layout";
import { Text } from "../../../styledCpns/DataDisplay";
import { Select } from "../../../styledCpns/Inputs";
import { StarLine } from "../../CommonCpns";
import StatsTable from "../../Readers/StatsTable";
import { levels } from "../../../data/misc";
import { deepCopy } from "../../../helpers/misc";

export default function SectionAttr(props) {
  const { charData, char } = props;
  const { setMyChars } = useContext(SetDBCt);
  return (
    <CardCarpet>
      <Text variant="h1" color={charData.vision}>
        {char.name}
      </Text>
      <StarLine rarity={charData.rarity} />
      <div className="ml-1 my-1 flex">
        <Text className="mr-1" variant="h5">
          Level
        </Text>
        <Select
          color={charData.vision}
          fontSize="1.25rem"
          fontWeight={700}
          align="right"
          value={char.level}
          onChange={(e) =>
            setMyChars((prev) => {
              const level = e.target.value;
              const maxLv = +level.slice(-2);
              const newChars = deepCopy(prev);
              newChars[props.charI].level = level;
              newChars[props.charI].ascension =
                maxLv === 20 ? 0 : (maxLv - 30) / 10;
              return newChars;
            })
          }
        >
          {levels.map((lv, i) => (
            <option key={i}>{lv}</option>
          ))}
        </Select>
      </div>
      <Box className="custom-sb" width="18.75rem" height="22.467rem">
        <StatsTable stats={props.ATTRs} breakdown={true} />
      </Box>
    </CardCarpet>
  );
}
