import ConsList from "../../Readers/ConsList";
import { deepCopy } from "../../../helpers/misc";

export default function Constellation(props) {
  return (
    <ConsList
      char={props.char}
      handleClick={(i) => {
        props.setChar((prev) => {
          const newChar = deepCopy(prev);
          newChar.constellation = newChar.constellation === i + 1 ? i : i + 1;
          return newChar;
        });
      }}
    />
  );
}
