import { useContext } from "react";
import { PartiesCt, SetupCt } from "../index";
import TalentList from "../../Readers/TalentList";
import { deepCopy } from "../../../helpers/misc";

export default function Talents(props) {
  const parties = useContext(PartiesCt);
  const setupI = useContext(SetupCt);
  return (
    <TalentList
      char={props.char}
      party={parties[setupI]}
      upgradeTl={(tlType, lv) =>
        props.setChar((prev) => {
          const char = deepCopy(prev);
          char[tlType] = lv;
          return char;
        })
      }
    />
  );
}
