import { Box } from "../../styledCpns/Layout";
import { Img } from "../../styledCpns/DataDisplay";
import { wikia, artifactIcons } from "../../data/misc";

export default function TypeFilter({ types, setTypes }) {
  return (
    <div className="mx-1 flex align-center">
      {Object.entries(artifactIcons).map((pair, i) => {
        const index = types.indexOf(pair[0]);
        return (
          <Box
            key={i}
            className="mr-3 p-1 pointer glow-on-hover"
            borderRadius="50%"
            bgColor={index !== -1 ? "green" : "none"}
            onClick={() =>
              setTypes((prev) => {
                const newTypes = [...prev];
                if (index !== -1) {
                  newTypes.splice(index, 1);
                } else {
                  newTypes.push(pair[0]);
                }
                return newTypes;
              })
            }
          >
            <Img width="2rem" src={wikia + pair[1] + ".png"} alt="" />
          </Box>
        );
      })}
    </div>
  );
}
