import styled from "styled-components";
import { colorCodes } from "../data/misc";

export const Select = styled(
  ({
    borderRadius,
    bgColor,
    color,
    fontSize,
    fontWeight,
    align,
    optColor,
    optFS,
    ...other
  }) => (
    <div>
      <select {...other} />
    </div>
  )
)`
  height: 100%;
  width: 100%;
  display: block;
  border: none;
  border-radius: ${(props) => props.borderRadius};
  background-color: ${(props) =>
    colorCodes[props.bgColor] || props.bgColor || "transparent"};
  color: ${(props) => colorCodes[props.color] || props.color};
  font-family: "Nunito", "Helvetica", "Roboto", "Arial", sans-serif;
  font-size: ${(props) => props.fontSize || "1rem"};
  font-weight: ${(props) => props.fontWeight};
  outline: none;
  text-align-last: ${(props) => props.align};
  option {
    color: ${(props) =>
      colorCodes[props.optColor] || props.optColor || "black"};
    font-size: ${(props) => props.optFS || "1rem"};
  }
  &.artSS-select {
    padding: 0 0.5rem 0 2.5rem;
    background-position: 0.5rem;
  }
  &.calc-main-select {
    background-origin: content-box;
    background-position: 100%;
  }
`;

export const Button = styled.button`
  padding: ${(props) => props.padding || "0.25rem 1rem"};
  border: none;
  border-radius: ${(props) => props.borderRadius || "1rem"};
  background-color: ${(props) => colorCodes[props.bgColor] || props.bgColor};
  color: ${(props) => colorCodes[props.color] || props.color};
  background-color: ${(props) =>
    colorCodes[
      {
        positive: "lightGold",
        negative: "red"
      }[props.variant]
    ]};
  color: ${(props) =>
    colorCodes[
      {
        positive: "black",
        negative: "white"
      }[props.variant]
    ]};
  font-family: "Nunito", "Helvetica", "Roboto", "Arial", sans-serif;
  font-weight: ${(props) => props.fontWeight || 700};
  font-size: ${(props) => props.fontSize || "1rem"};
  box-shadow: var(--cmn-shadow);
  &:not(.unavailable) {
    cursor: pointer;
  }
`;

export const IconBtn = styled(
  ({ position, top, bottom, left, right, icon, size, ...others }) => (
    <Button padding="0" {...others}>
      <i className={icon} />
    </Button>
  )
)`
  position: ${(props) => props.position};
  top: ${(props) => props.top};
  bottom: ${(props) => props.bottom};
  left: ${(props) => props.left};
  right: ${(props) => props.right};
  width: ${(props) => props.size};
  height: ${(props) => props.size};
  border-radius: ${(props) => props.borderRadius || "50%"};
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const CloseBtn = (props) => (
  <IconBtn
    icon="fas fa-times"
    position="absolute"
    top="0.5rem"
    right="0.5rem"
    size="1.75rem"
    {...props}
  />
);

export const TextInput = styled.input`
  border-radius: ${(props) => props.borderRadius};
  font-size: ${(props) => props.fontSize || "1rem"};
  text-align: ${(props) => props.align};
`;

export const TextArea = styled.textarea`
  border-radius: 0.25rem;
  border: none;
  font-size: 1rem;
  word-break: break-all;
  resize: none;
  outline: none;
  :focus {
    box-shadow: 0 0 0 3px ${colorCodes.orange};
  }
`;
