import StatsTable from "../../Readers/StatsTable";
import { Box } from "../../../styledCpns/Layout";
import { Text } from "../../../styledCpns/DataDisplay";
import { SetBonus } from "../../CommonCpns";

export default function Stats(props) {
  return (
    <div className="full-h flex">
      <Box className="px-3 pt-2 pb-3 flex-col" width="20rem">
        <Text variant="h6" fontWeight={700} color="orange">
          Final Attributes
        </Text>
        <div className="mt-1 grow-1 custom-sb">
          <StatsTable stats={props.ATTRs} breakdown={true} />
        </div>
      </Box>
      <Box className="px-3 pt-2 pb-3 flex-col" width="20rem">
        <Text variant="h6" fontWeight={700} color="orange">
          Artifact Stats
        </Text>
        <Box className="mt-1 custom-sb" height="22rem">
          <StatsTable stats={props.artSBnes} breakdown={false} />
        </Box>
        <Box className="mt-3 hide-sb" height="11.2rem">
          <SetBonus sets={props.sets} />
        </Box>
      </Box>
    </div>
  );
}
