import { useContext } from "react";
import { ResultCt, SetupCt } from "../index";
import { Box } from "../../../styledCpns/Layout";
import StatsTable from "../../Readers/StatsTable";

export default function Attributes() {
  const { allATTRs } = useContext(ResultCt);
  const curSetupI = useContext(SetupCt);
  return (
    <Box className="custom-sb" height="25.5rem">
      <StatsTable stats={allATTRs[curSetupI]} breakdown={true} />
    </Box>
  );
}
