import { useContext, useState } from "react";
import { MyArtsCt, SetDBCt } from "../../App";
import { Button, Select } from "../../styledCpns/Inputs";
import { MyStyledRack } from "../../styledCpns/Layout";
import Thumb from "../Readers/Thumb";
import artifacts from "../../data/artifacts";
import { elmtDmgBnes } from "../../data/misc";
import TypeFilter from "./TypeFilter";
import { deepCopy } from "../../helpers/misc";

const stats = [
  "No Priority",
  "ATK",
  "ATK%",
  "DEF",
  "DEF%",
  "HP",
  "HP%",
  "CRIT Rate",
  "CRIT DMG",
  "Elemental Mastery",
  "Energy Recharge",
  "Healing Bonus",
  "Physical DMG Bonus",
  ...elmtDmgBnes
];

export default function LeftSection(props) {
  const myArts = useContext(MyArtsCt);
  const { setMyArts } = useContext(SetDBCt);
  const [clickedI, setClickedI] = useState(null);
  return (
    <MyStyledRack className="hide-sb">
      <div className="btn_bar flex align-center">
        <Button
          className="mr-3 glow-on-hover"
          variant="positive"
          onClick={() => props.setPrePickerOn(true)}
        >
          Add
        </Button>
        <Button
          className="mr-3 glow-on-hover"
          variant="positive"
          onClick={() =>
            setMyArts((prev) => {
              const newArts = deepCopy(prev);
              newArts.sort((a, b) => sortArtifacts(a, b));
              return newArts;
            })
          }
        >
          Sort
        </Button>
        <TypeFilter types={props.types} setTypes={props.setTypes} />
        <div className="mr-3">
          <Select
            className="px-2 pt-2 pb-1"
            color="white"
            optColor="white"
            bgColor="cardBg3"
            value={props.set}
            onChange={(e) => props.setSet(e.target.value)}
          >
            {["All", ...getSets(myArts)].map((opt, i) => (
              <option key={i}>{opt}</option>
            ))}
          </Select>
        </div>
        <Select
          className="px-2 pt-2 pb-1"
          color="white"
          optColor="white"
          bgColor="cardBg3"
          value={props.stat}
          onChange={(e) => props.setStat(e.target.value)}
        >
          {stats.map((opt, i) => (
            <option key={i}>{opt}</option>
          ))}
        </Select>
      </div>
      <div className="flex-wrap">
        {props.arts.map((art, i) => {
          const { icon } = artifacts[art.set][art.type];
          return (
            <div key={i} className="cell relative">
              <Thumb
                item={art}
                icon={icon}
                rarity="Gold"
                chosen={i === props.chosenI}
                clicked={i === clickedI}
                handleMouseDown={() => setClickedI(i)}
                choose={() => {
                  setClickedI(null);
                  props.setChosenI(i);
                }}
              />
            </div>
          );
        })}
      </div>
    </MyStyledRack>
  );
}

function sortArtifacts(a, b) {
  if (a.level !== b.level) {
    return b.level - a.level;
  }
  if (a.type !== b.type) {
    const type = {
      flower: 5,
      plume: 4,
      sands: 3,
      goblet: 2,
      circlet: 1
    };
    return type[b.type] - type[a.type];
  }
  return b.set.localeCompare(a.set);
}

function getSets(arts) {
  const result = [];
  for (let art of arts) {
    if (!result.includes(art.set)) {
      result.push(art.set);
    }
  }
  return result;
}
