import { useState, useContext, useMemo } from "react";
import { MyArtsCt, SetDBCt } from "../../App";
import { Box } from "../../styledCpns/Layout";
import PrePicker from "../Picker/PrePicker";
import ArtPicker from "../Picker/ArtPicker";
import LeftSection from "./LeftSection";
import RightSection from "./RightSection";
import { artMS } from "../../data/artifacts/config";
import { artifactIcons, artTypes } from "../../data/misc";
import { deepCopy } from "../../helpers/misc";

export default function MyArtifacts() {
  const [prePickerOn, setPrePickerOn] = useState(false);
  const [artType, setArtType] = useState(null);
  const [chosenI, setChosenI] = useState(0);
  const myArts = useContext(MyArtsCt);
  const { setMyArts } = useContext(SetDBCt);

  const [types, setTypes] = useState(artTypes);
  const [set, setSet] = useState("All");
  const [stat, setStat] = useState("Level");
  const arts = useMemo(() => getArts(myArts, types, set, stat), [
    myArts,
    types,
    set,
    stat
  ]);

  return (
    <Box className="full-h flex-center" bgColor="cardBg2">
      <div className="flex relative">
        <LeftSection
          setPrePickerOn={setPrePickerOn}
          chosenI={chosenI}
          setChosenI={setChosenI}
          types={types}
          setTypes={setTypes}
          set={set}
          setSet={setSet}
          arts={arts}
          stat={stat}
          setStat={setStat}
        />
        <RightSection art={arts[chosenI]} />
      </div>
      {prePickerOn && (
        <PrePicker
          close={() => setPrePickerOn(false)}
          choices={artifactIcons}
          pick={(artType) => {
            setArtType(artType);
            setPrePickerOn(false);
          }}
        />
      )}
      {artType && (
        <ArtPicker
          needMassAdd={true}
          artType={artType}
          close={() => setArtType(null)}
          pick={(newItem) => {
            setMyArts((prev) => {
              const newArts = deepCopy(prev);
              newArts.unshift({ ID: Date.now(), user: null, ...newItem });
              return newArts;
            });
          }}
        />
      )}
    </Box>
  );
}

function searchValue(art, type) {
  if (art.mainSType === type) {
    return artMS[art.type][art.mainSType][art.level];
  }
  for (let stat of art.subS) {
    if (stat.type === type) {
      return stat.val;
    }
  }
  return 0;
}

function getArts(myArts, types, set, stat) {
  let result =
    types.length === 5
      ? [...myArts]
      : myArts.filter((art) => types.includes(art.type));
  if (set !== "All") {
    result = result.filter((art) => art.set === set);
  }
  if (stat !== "No Priority") {
    result.sort((a, b) => searchValue(b, stat) - searchValue(a, stat));
  }
  return result;
}
