import { deepCopy } from "../../../helpers/misc";
import { Button } from "../../../styledCpns/Inputs";

export default function SortBtns({ setList }) {
  return (
    <div className="mt-2 flex justify-center">
      <Button
        className="mr-2 glow-on-hover"
        variant="positive"
        onClick={() =>
          setList((prev) => {
            const newList = deepCopy(prev);
            newList.sort((a, b) => a.name.localeCompare(b.name));
            return newList;
          })
        }
      >
        Name
      </Button>
      <Button
        className="mr-2 glow-on-hover"
        variant="positive"
        onClick={() =>
          setList((prev) => {
            const newList = deepCopy(prev);
            return newList.sort((a, b) => {
              const [fA, sA] = a.level.split("/").map((lv) => +lv);
              const [fB, sB] = b.level.split("/").map((lv) => +lv);
              if (fA !== fB) {
                return fB - fA;
              }
              return sB - sA;
            });
          })
        }
      >
        Level
      </Button>
      <Button
        className="glow-on-hover"
        variant="positive"
        onClick={() =>
          setList((prev) => {
            const newList = deepCopy(prev);
            return newList.sort((a, b) => {
              const rank = { Gold: 3, Purple: 2 };
              return rank[b.rarity] - rank[a.rarity];
            });
          })
        }
      >
        Rarity
      </Button>
    </div>
  );
}
