import { elmts } from "../../../data/elements";
import characters from "../../../data/characters";
import weapons from "../../../data/weapons";
import artifactBuffs from "../../../data/artifacts/artifactBuffs";

function getSelfBCs(charName) {
  const result = [];
  const { buffs } = characters[charName];
  if (buffs) {
    buffs.forEach((buff, index) => {
      if (buff.affect !== "teammates") {
        const node = { activated: false, index };
        if (buff.selfLabels) {
          node.inputs = [...buff.inputs];
        }
        result.push(node);
      }
    });
  }
  return result;
}

export function getCharDCs(charName, fromSelf) {
  const result = [];
  const { debuffs } = characters[charName];
  if (debuffs) {
    debuffs.forEach((debuff, index) => {
      if (!fromSelf && debuff.affect === "self") {
        return;
      }
      const node = { activated: false, index };
      const { selfLabels, labels, inputTypes } = debuff;
      if (fromSelf && selfLabels) {
        node.inputs = getInputs(selfLabels, inputTypes);
      } else if (!fromSelf && labels) {
        node.inputs = getInputs(labels, inputTypes);
      }
      result.push(node);
    });
  }
  return result;
}

// v2.3 - debuffs need inputs:
// From self: AnemoMC C6 & Venti C6
// Not from self: Eula E & Shenhe Q
function getInputs(labels, inputTypes) {
  const result = [];
  labels.forEach((_, i) => {
    const type = inputTypes[i];
    if (type === "absorption") {
      result.push("Pyro");
    } else if (type === "text") {
      // level
      result.push(1);
    }
  });
  return result;
}

// also used in decode
export function initSelfMCs(charName) {
  return {
    BCs: getSelfBCs(charName),
    DCs: getCharDCs(charName, true),
    infusion: []
  };
}

// also used in decode
export function getMainWpBCs(type, name) {
  let result = [];
  const { buffs } = weapons[type][name];
  if (buffs) {
    buffs.forEach((buff, index) => {
      if (buff.affect !== "teammates") {
        const node = { activated: false, index };
        if (buff.inputTypes) {
          node.inputs = [];
          buff.inputTypes.forEach((type, j) => {
            if (type === "stacks") {
              node.inputs.push(1);
            } else if (type === "check") {
              node.inputs.push(false);
            } else if (type === "choices") {
              node.inputs.push(buff.values[j][0]);
            }
          });
        }
        result.push(node);
      }
    });
  }
  return result;
}

export function initSubWpMCs() {
  return { BCs: {}, DCs: {} };
}

// also used in ./SetupManaget/SectionArts & decode
export function getMainArtBCs(setName) {
  const result = [];
  if (artifactBuffs[setName]) {
    artifactBuffs[setName].forEach((buff, index) => {
      if (buff.affect !== "teammates") {
        const node = { activated: false, index };
        if (buff.inputTypes) {
          node.inputs = [];
          buff.inputTypes.forEach((type, j) => {
            if (type === "stacks") {
              node.inputs.push(1);
            } else if (type === "crystallize") {
              node.inputs.push("Pyro");
            }
          });
        }
        result.push(node);
      }
    });
  }
  return result;
}

// also used in decode
// related to ./SetupManager/SectionArts => setArtPiece()
export function getSubArtBCs(mainSet) {
  const result = [];
  for (let set in artifactBuffs) {
    if (set !== mainSet) {
      artifactBuffs[set].forEach((buff, index) => {
        if (buff.affect !== "self") {
          const node = { set, activated: false, index };
          if (buff.inputTypes) {
            node.inputs = [];
            buff.inputTypes.forEach((type, j) => {
              if (type === "crystallize") {
                node.inputs.push("Pyro");
              }
            });
          }
          result.push(node);
        }
      });
    }
  }
  return result;
}

// also used in decode
export function getSubArtDCs() {
  return [
    { set: "Viridescent Venerer", activated: false, index: 0, inputs: ["Pyro"] }
  ];
}

// also used in decode
export function initParty() {
  return [
    { name: null, BCs: [], DCs: [], infusion: null },
    { name: null, BCs: [], DCs: [], infusion: null },
    { name: null, BCs: [], DCs: [], infusion: null }
  ];
}

export function initElmtMCs() {
  return {
    ampRxn: null,
    superconduct: false,
    resonance: []
  };
}

export function initTarget() {
  const result = { Level: 1, "Physical RES": 10 };
  for (let elmt of elmts) {
    result[elmt + " RES"] = 10;
  }
  return result;
}
