import { infusePrior } from "../data/elements";

export function getArtSets(pieces = []) {
  const sets = [];
  const count = {};
  for (let artP of pieces) {
    if (artP) {
      const name = artP.set;
      count[name] = (count[name] || 0) + 1;
      if (count[name] === 2) {
        sets.push({ name, bonusLv: 0 });
      } else if (count[name] === 4) {
        sets[0].bonusLv = 1;
      }
    }
  }
  return sets;
}

export function processNumInp(inp, before, max) {
  if (inp === "") return 0;
  if (!isNaN(inp) && inp >= 0 && inp <= max) {
    if (inp.slice(-1) === ".") return inp;
    return Math.round(inp * 10) / 10;
  }
  return before;
}

export function getFinalInfusion(MCs, ownVision, party) {
  const result = {};
  for (let type of ["NA", "CA", "PA"]) {
    result[type] = strongestInfusion(type, MCs.infusion, ownVision, party);
  }
  return result;
}

function strongestInfusion(type, infusion, ownVision, party) {
  const contenders = ["Physical"];
  for (let ifs of infusion) {
    if (ifs.range.includes(type)) {
      if (!ifs.canBeOverrided) {
        return ownVision;
      } else if (!contenders.includes(ownVision)) {
        contenders.unshift(ownVision);
      }
    }
  }
  for (let teammate of party) {
    if (teammate.infusion) {
      for (let i in contenders) {
        if (
          infusePrior.indexOf(teammate.infusion.elmt) <
          infusePrior.indexOf(contenders[i])
        ) {
          contenders.splice(i, 0, teammate.infusion.elmt);
          break;
        }
      }
    }
  }
  return contenders[0];
}
