import styled from "styled-components";
import { useState } from "react";
import { Text } from "../../styledCpns/DataDisplay";
import { CloseBtn, IconBtn } from "../../styledCpns/Inputs";
import { Box } from "../../styledCpns/Layout";
import Filter from "./Filter";
import { artNames } from "../../data/misc";

export default function Heading(props) {
  const [filterOn, setFilterOn] = useState(false);
  const { dataType, massAdd } = props;
  return (
    <Box
      className="full-h flex align-center relative"
      borderRadius="0.5rem 0.5rem 0 0"
      bgColor="orange"
    >
      <Text className="full-w" variant="h4" align="center" color="black">
        {artNames[dataType] || dataType}
      </Text>
      {dataType === "Character" && (
        <>
          <FilterBtn
            className="glow-on-hover"
            handleClick={() => setFilterOn(!filterOn)}
          />
          <Filter
            filterOn={filterOn}
            setFilterOn={setFilterOn}
            filter={props.filter}
            setFilter={props.setFilter}
          />
        </>
      )}
      {window.innerWidth >= 610 && props.needMassAdd && (
        <MassAddArea className="flex align-center">
          <Text className="mt-1 mr-3" fontWeight={700} color="black">
            Mass Add
          </Text>
          <input
            type="checkbox"
            value={massAdd}
            onChange={() => props.setMassAdd(!massAdd)}
          />
        </MassAddArea>
      )}
      <CloseBtn
        className="glow-on-hover"
        bgColor="black"
        color="white"
        onClick={props.close}
      />
    </Box>
  );
}

const FilterBtn = styled(({ handleClick, ...others }) => (
  <IconBtn
    icon="fas fa-filter"
    position="absolute"
    top="50%"
    left="20px"
    size="1.75rem"
    fontSize="0.825rem"
    bgColor="black"
    color="lightGold"
    {...others}
    onClick={handleClick}
  />
))`
  margin-top: -0.825rem;
`;

const MassAddArea = styled.div`
  position: absolute;
  right: 6rem;
  input {
    transform: scale(1.6);
  }
`;
