import { useContext, useState } from "react";
import { SetDBCt, MyStsCt } from "../../../../App";
import { ModalCarpet, Box } from "../../../../styledCpns/Layout";
import { TextInput } from "../../../../styledCpns/Inputs";
import { Text } from "../../../../styledCpns/DataDisplay";
import { YinYangBtnBar, ShareLayout } from "../../../CommonCpns";
import {
  SelfMCsCt,
  WpRackCt,
  SubWpMCsCt,
  ArtChestCt,
  PartiesCt,
  ElmtMCsCt,
  TargetCt
} from "../../index";
import { useCloseModal } from "../../../../helpers/hooks";

export function SaveCtrl(props) {
  const { char, savedI, close } = props;
  const { name, ID, index } = props.tempSetups[savedI];
  const selfMCs = useContext(SelfMCsCt);
  const wpRack = useContext(WpRackCt);
  const subWpMCs = useContext(SubWpMCsCt);
  const artChest = useContext(ArtChestCt);
  const parties = useContext(PartiesCt);
  const elmtMCs = useContext(ElmtMCsCt);
  const { target } = useContext(TargetCt);
  const { setMySts, setTab, setChosenStID } = useContext(SetDBCt);
  const mySetups = useContext(MyStsCt);
  const existedI = mySetups.findIndex((st) => st.ID === ID);
  const [input, setInput] = useState(
    existedI >= 0 ? mySetups[existedI].name : char.name + " - " + name
  );

  const saveSetup = () => {
    setMySts((prev) => {
      const newSts = [...prev];
      const newSetup = {
        name: input,
        ID,
        type: "original",
        char,
        selfMCs: selfMCs[index],
        weapon: wpRack[index],
        subWpMCs: subWpMCs[index],
        art: artChest[index],
        party: parties[index],
        elmtMCs: elmtMCs[index],
        target
      };
      if (existedI >= 0) newSts[existedI] = newSetup;
      else newSts.unshift(newSetup);
      return newSts;
    });
    props.closeSettings();
    setChosenStID(ID);
    setTab("Setups");
  };

  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="p-4 flex-col white-glow-shadow"
        borderRadius="0.5rem"
        bgColor="cardBg1"
      >
        <Text
          className="mb-2 mx-auto"
          variant="h5"
          color="orange"
          fontWeight={700}
        >
          {existedI >= 0 ? "Modify OLD" : "Save NEW"} Setup
        </Text>
        <TextInput
          className="mb-5 px-3 pt-3 pb-2"
          type="text"
          borderRadius="0.5rem"
          fontSize="1.5rem"
          align="center"
          value={input}
          onChange={(e) => {
            const { value } = e.target;
            if (value.length <= 32) setInput(e.target.value);
          }}
        />
        <YinYangBtnBar
          leftText="Cancel"
          leftClicked={close}
          rightText="Confirm"
          rightClicked={() => {
            saveSetup();
            close();
          }}
        />
      </Box>
    </ModalCarpet>
  );
}

export function SharedCtrl(props) {
  const { char, sharedI, close } = props;
  const { name, index } = props.tempSetups[sharedI];
  const selfMCs = useContext(SelfMCsCt);
  const wpRack = useContext(WpRackCt);
  const subWpMCs = useContext(SubWpMCsCt);
  const artChest = useContext(ArtChestCt);
  const parties = useContext(PartiesCt);
  const elmtMCs = useContext(ElmtMCsCt);
  const { target } = useContext(TargetCt);
  const data = {
    char,
    selfMCs: selfMCs[index],
    weapon: wpRack[index],
    subWpMCs: subWpMCs[index],
    art: artChest[index],
    party: parties[index],
    elmtMCs: elmtMCs[index],
    target
  };
  return <ShareLayout setupName={name} data={data} close={close} />;
}
