import { useState, useContext, useEffect, useRef } from "react";
import { MyStsCt, SetDBCt } from "../../App";
import { Box } from "../../styledCpns/Layout";
import Setup from "./Setup";
import DmgResults from "./DmgResults";
import { Confirmation, ShareLayout } from "../CommonCpns";
import characters from "../../data/characters";
import { getFinalInfusion } from "../../helpers/processors";
import { deepCopy } from "../../helpers/misc";
import getBuffedStats from "../../calculators/buffedStats";
import getDmg from "../../calculators/damage";

export default function MySetups(props) {
  const { setMySts, setChosenStID, setImportedSt } = useContext(SetDBCt);
  const mySetups = useContext(MyStsCt);
  const chosenStI = mySetups.findIndex((st) => st.ID === props.chosenStID);
  const chosenSt = mySetups[chosenStI];
  const [removedI, setRemovedI] = useState(null);
  const [sharedI, setSharedI] = useState(null);
  const ref = useRef();

  useEffect(() => {
    ref.current.scrollTop = 326 * chosenStI;
  }, []);

  let infusion;
  let ATTRs;
  let artSBnes;
  let rxnBnes;
  let dmg;

  if (chosenSt) {
    const {
      char,
      selfMCs,
      weapon,
      subWpMCs,
      art,
      elmtMCs,
      party,
      target
    } = chosenSt;
    const charData = characters[char.name];
    infusion = getFinalInfusion(selfMCs, charData.vision, party);
    let bonuses;
    const stats = getBuffedStats(
      charData,
      char,
      selfMCs.BCs,
      weapon,
      subWpMCs.BCs,
      art,
      elmtMCs.resonance,
      party,
      infusion
    );
    ATTRs = stats.ATTRs;
    artSBnes = stats.artSBnes;
    rxnBnes = stats.rxnBnes;
    bonuses = stats.bonuses;
    dmg = getDmg(
      deepCopy(char),
      selfMCs,
      party,
      weapon,
      art.subDCs,
      ATTRs,
      bonuses,
      rxnBnes,
      infusion,
      elmtMCs,
      target
    );
  }
  return (
    <Box className="full-h flex-center" bgColor="cardBg2">
      <Box className="flex" height="600px">
        <Box
          ref={ref}
          className="pl-2 pb-2 flex-col align-start overflow-auto"
          width="41rem"
          style={{ scrollBehavior: "smooth" }}
        >
          {mySetups.map((setup, i) => (
            <Setup
              key={i}
              setup={setup}
              choose={() => {
                setChosenStID(setup.ID);
                ref.current.scrollTop = 326 * i;
              }}
              chosen={i === chosenStI}
              edit={() => setImportedSt(setup)}
              remove={() => setRemovedI(i)}
              share={() => setSharedI(i)}
              result={{ infusion, ATTRs, artSBnes, rxnBnes }}
            />
          ))}
        </Box>
        <div>
          <DmgResults setup={chosenSt} dmg={dmg} />
        </div>
      </Box>
      {removedI !== null && (
        <Confirmation
          message={
            <>
              Do you wish to remove "<b>{mySetups[removedI].name}</b>"?
            </>
          }
          confirm={() =>
            setMySts((prev) => {
              const newSts = [...prev];
              const index = newSts.findIndex(
                (st) => st.ID === mySetups[removedI].ID
              );
              newSts.splice(index, 1);
              return newSts;
            })
          }
          close={() => setRemovedI(null)}
        />
      )}
      {sharedI !== null && (
        <ShareCtrl setup={mySetups[sharedI]} close={() => setSharedI(null)} />
      )}
    </Box>
  );
}

function ShareCtrl(props) {
  const { name, ID, type, ...data } = props.setup;
  return <ShareLayout setupName={name} data={data} close={props.close} />;
}
