import { useState, useEffect } from "react";
import { Box } from "../../styledCpns/Layout";
import { Text } from "../../styledCpns/DataDisplay";
import OneSetup from "../Calculator/DmgResults/OneSetup";
import { DmgTable } from "../CommonCpns";
import { getNamesForTables } from "../../helpers/misc";

export default function DmgResults({ setup, dmg }) {
  const [closed, setClosed] = useState([]);
  const ID = setup?.ID ? setup.ID : "";
  useEffect(() => {
    setClosed([]);
  }, [ID]);

  if (!setup) {
    return (
      <Box
        className="ml-3 full-h"
        width="21.75rem"
        borderRadius="0.5rem"
        bgColor="cardBg3"
      />
    );
  }
  const namesForTables = getNamesForTables(setup.char);
  return (
    <Box
      className="ml-3 px-3 pt-2 pb-4 full-h flex-col"
      width="21.75rem"
      borderRadius="0.5rem"
      bgColor="cardBg3"
    >
      <Text className="mx-3" fontWeight={700} align="center">
        {setup.name}
      </Text>
      <Box className="mt-2 grow-1 hide-sb">
        {namesForTables.map((name, i) => {
          const stdValues = dmg[name.main];
          return (
            <DmgTable
              key={i}
              index={i}
              heading={name.main}
              closed={closed}
              setClosed={setClosed}
              mb={i === namesForTables.length - 1 ? "" : " mb-3"}
            >
              <OneSetup subNames={name.subs} stdValues={stdValues} />
            </DmgTable>
          );
        })}
      </Box>
    </Box>
  );
}
