import styled from "styled-components";
import { Text } from "../../../styledCpns/DataDisplay";
import { Box, CardCarpet } from "../../../styledCpns/Layout";
import { ActionBtn } from "../../CommonCpns";
import Members from "./Members";
import Gears from "./Gears";
import TopBar from "./TopBar";
import characters from "../../../data/characters";
import { colorCodes } from "../../../data/misc";

const StyledDiv = styled.div`
  border-left: 2px solid ${colorCodes.cardBg3};
`;

export default function Setup(props) {
  const { setup } = props;
  const { char } = props.setup;
  const charData = characters[char.name];
  return (
    <Box
      className="mt-3 px-2 pt-3 pb-2"
      borderRadius="0.5rem"
      bgColor="cardBg3"
      boxShadow={props.chosen ? "0 0 5px 5px green" : "var(--cmn-shadow)"}
      onClick={props.choose}
    >
      <div className="px-2 flex justify-between">
        <Box className="flex align-center" width="24rem">
          <Text className="ellipsis" variant="h3" color="orange">
            {setup.name}
          </Text>
        </Box>
        <div className="flex pb-3">
          <ActionBtn
            icon="fas fa-wrench"
            className="mr-4 p-2 glow-on-hover"
            variant="positive"
            onClick={props.edit}
          />
          <ActionBtn
            icon="fas fa-share-alt"
            className="mr-4 p-2 glow-on-hover"
            bgColor="green"
            onClick={props.share}
          />
          <ActionBtn
            icon="fas fa-trash-alt"
            className="glow-on-hover"
            variant="negative"
            onClick={props.remove}
          />
        </div>
      </div>
      <div className="flex">
        <CardCarpet className="mr-1 flex">
          <Members char={char} charData={charData} party={setup.party} />
          <StyledDiv className="ml-3 pl-3">
            <TopBar result={props.result} setup={setup} />
            <Gears weapon={setup.weapon} artPieces={setup.art.pieces} />
          </StyledDiv>
        </CardCarpet>
      </div>
    </Box>
  );
}
