import styled from "styled-components";
import { useContext } from "react";
import { ArtStatsCt } from "./index";
import { Box } from "../../../styledCpns/Layout";
import { IconBtn } from "../../../styledCpns/Inputs";
import StatsTable from "../../Readers/StatsTable";

const StyledDiv = styled.div`
  position: absolute;
  top: 1rem;
`;

export default function ArtAllStats(props) {
  const artSBnes = useContext(ArtStatsCt);
  return (
    <StyledDiv>
      <Box bgColor="cardBg1">
        <Box className="custom-sb" width="18.75rem" height="25.6rem">
          <StatsTable stats={artSBnes} />
        </Box>
        <div className="mt-3 flex justify-center">
          <IconBtn
            icon="fas fa-times"
            size="1.75rem"
            variant="negative"
            onClick={props.close}
          />
        </div>
      </Box>
    </StyledDiv>
  );
}
