import { useContext, useState } from "react";
import { MyWpsCt, SetDBCt } from "../../App";
import { Button } from "../../styledCpns/Inputs";
import { MyStyledRack, Box } from "../../styledCpns/Layout";
import Thumb from "../Readers/Thumb";
import weapons from "../../data/weapons";
import { deepCopy } from "../../helpers/misc";

export default function LeftSection(props) {
  const myWps = useContext(MyWpsCt);
  const { setMyWps } = useContext(SetDBCt);
  const [clickedI, setClickedI] = useState(null);
  return (
    <MyStyledRack className="hide-sb">
      <Box className="btn_bar flex align-center" height="2.5rem">
        <Button
          className="mr-3 glow-on-hover"
          variant="positive"
          onClick={() => props.setPrePickerOn(true)}
        >
          Add
        </Button>
        <Button
          className="glow-on-hover"
          variant="positive"
          onClick={() =>
            setMyWps((prev) => {
              const newWps = deepCopy(prev);
              newWps.sort((a, b) => sortWeapons(a, b));
              return newWps;
            })
          }
        >
          Sort
        </Button>
      </Box>
      <div className="flex-wrap">
        {myWps.map((weapon, i) => {
          const { icon, rarity } = weapons[weapon.type][weapon.name];
          return (
            <div key={i} className="cell relative">
              <Thumb
                item={weapon}
                icon={icon}
                rarity={rarity}
                chosen={i === props.chosenI}
                clicked={i === clickedI}
                handleMouseDown={() => setClickedI(i)}
                choose={() => {
                  setClickedI(null);
                  props.setChosenI(i);
                }}
              />
            </div>
          );
        })}
      </div>
    </MyStyledRack>
  );
}

function sortWeapons(a, b) {
  const rA = weapons[a.type][a.name].rarity;
  const rB = weapons[b.type][b.name].rarity;
  if (rA !== rB) {
    const rank = {
      Gold: 5,
      Purple: 4,
      Blue: 3,
      Green: 2,
      Gray: 1
    };
    return rank[rB] - rank[rA];
  }
  const [fA, sA] = a.level.split("/").map((lv) => +lv);
  const [fB, sB] = b.level.split("/").map((lv) => +lv);
  if (fA !== fB) {
    return fB - fA;
  }
  if (a.type !== b.type) {
    const type = {
      Bow: 5,
      Catalyst: 4,
      Polearm: 3,
      Claymore: 2,
      Sword: 1
    };
    return type[b.type] - type[a.type];
  }
  return sB - sA;
}
