import { levels, pctSTypes } from "../data/misc";
import { tfmRxns } from "../data/elements";
import characters from "../data/characters";

export function mapToLevels(arr) {
  const result = {};
  levels.forEach((lv, i) => {
    result[lv] = arr[i];
  });
  return result;
}

export const pctOf = (type) => (pctSTypes.includes(type) ? "%" : "");

export function deepCopy(item) {
  return JSON.parse(JSON.stringify(item));
}

export function getNamesForTables(char) {
  const result = characters[char.name].actvTalents.map((talent) => ({
    main: talent.type,
    subs: talent.stats.map(({ name }) => name)
  }));
  result.push({
    main: "Reactions DMG",
    subs: Object.keys(tfmRxns)
  });
  // if (cmpSetupIs.length === 1) {
  //   const { pasvProcD } = weapons[weapon.type][weapon.name];
  //   if (pasvProcD) {
  //     const node = { name: "Weapon Attack Proc", subNames: [] };
  //     pasvProcD.forEach((dmg) => {
  //       node.subNames.push(dmg.name);
  //     });
  //     result.push(node);
  //   }
  // }
  return result;
}
