import { useState } from "react";
import { Box } from "../../../styledCpns/Layout";
import { Button, Select } from "../../../styledCpns/Inputs";
import { Img, Text } from "../../../styledCpns/DataDisplay";
import { StarLine } from "../../CommonCpns";
import { Card, MainSelect } from "../styles";
import Attributes from "./Attributes";
import Weapon from "./Weapon";
import Artifacts from "./Artifacts";
import Constellation from "./Constellation";
import Talents from "./Talents";
import CharPicker from "./CharPicker";
import { wikia, levels } from "../../../data/misc";

export default function CalcChar(props) {
  const { char, charData, setChar } = props;
  const [tab, setTab] = useState("Attributes");
  const [pickerOn, setPickerOn] = useState(false);
  return (
    <Card className="px-4 py-3" bgColor="cardBg1">
      <div className="mb-3 flex justify-center">
        <Button
          className="glow-on-hover"
          variant="positive"
          onClick={() => setPickerOn(true)}
        >
          Choose New Character
        </Button>
      </div>
      <Box height="7.5rem">
        {char !== null && (
          <div className="flex justify-center">
            <Img
              className="mr-3"
              width="6.25rem"
              src={wikia + charData.icon + ".png"}
              alt={char.name}
            />
            <div className="overflow-hidden">
              <Text className="ellipsis" variant="h1" color={charData.vision}>
                {char.name}
              </Text>
              <StarLine className="mt-1" rarity={charData.rarity} />
              <div className="mt-1 flex">
                <Text variant="h6" className="mr-1">
                  Level
                </Text>
                <Select
                  color={charData.vision}
                  fontSize="1.125rem"
                  fontWeight={700}
                  align="right"
                  value={char.level}
                  onChange={(e) =>
                    setChar((prev) => {
                      const level = e.target.value;
                      const maxLv = +level.slice(-2);
                      return {
                        ...prev,
                        level,
                        ascension: maxLv === 20 ? 0 : (maxLv - 30) / 10
                      };
                    })
                  }
                >
                  {levels.map((level) => (
                    <option key={level}>{level}</option>
                  ))}
                </Select>
              </div>
            </div>
          </div>
        )}
      </Box>
      <Box height="28.2rem">
        {char !== null && (
          <MainSelect
            tab={tab}
            setTab={setTab}
            options={[
              "Attributes",
              "Weapon",
              "Artifacts",
              "Constellation",
              "Talents"
            ]}
          />
        )}
        {char !== null && (
          <div className="mt-2">
            {
              {
                Attributes: <Attributes />,
                Weapon: <Weapon />,
                Artifacts: <Artifacts />,
                Constellation: <Constellation char={char} setChar={setChar} />,
                Talents: <Talents char={char} setChar={setChar} />
              }[tab]
            }
          </div>
        )}
      </Box>
      {pickerOn && (
        <CharPicker close={() => setPickerOn(false)} setChar={setChar} />
      )}
    </Card>
  );
}
